@media screen and (max-width: 960px){

    body {
        font-size: $bodyTablet;
    }

    /* Grid */

    .content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 4%;
    }

    .page-title{
        grid-column: 1 / 4;
        padding-bottom: $desktopspaceMedium;
    }

    .full-width {
        grid-column: 1 / 4;
        padding-bottom: $desktopspaceLarge;
    }

    .left-2{
        grid-column: 1 / 3;
        padding-bottom: $desktopspaceLarge;
    }

    .right-2{
        grid-column: 2 / 4;
        padding-bottom: $desktopspaceLarge;
    }

    .left-1{
        grid-column: 1 / 2;
        padding-bottom: $desktopspaceLarge;
    }

    .center-1{
        grid-column: 2 / 3;
        padding-bottom: $desktopspaceLarge;
    }

    .right-1{
        grid-column: 3 / 4;
        padding-bottom: $desktopspaceLarge;
    }
}

@media screen and (max-width: 540px){

    body {
        font-size: $bodyMobile;
    }

    .site-wrapper{
        padding: 0 $mobilespaceSmall 0 $mobilespaceSmall;
    }

    .nav-right {
        display: none;
    }

        /* Grid */

        .content {
            display: inline
            }
        
            .page-title{
                padding-bottom: $mobilespaceSmall;
            }
        
            .full-width {
                padding-bottom: $mobilespaceLarge;
            }
        
            .left-2{
                padding-bottom: $mobilespaceLarge;
            }
        
            .right-2{
                padding-bottom: $mobilespaceLarge;
            }
        
            .left-1{
                padding-bottom: $mobilespaceLarge;
            }
        
            .center-1{
                padding-bottom: $mobilespaceLarge;
            }
        
            .right-1{
                padding-bottom: $mobilespaceLarge;
            }

}

@media screen and (max-width: 340px){
    h1 {
        font-size: 5em;
    }

}




