/* Root */

$backgroundColor: #1A1A1A;
$bodyColor: #fff;
$textMuted: #959595;
$textYellow: #FFFE4D;
$textBlue: #66E3FF ;
$textPurple: #D980FF ;
$bodyFont: "sohne" ,Arial,sans-serif,"Apple Color Emoji";

/* Desktop Styles */

$bodyDesktop: 34px; 
$desktopspaceXS: 10px;
$desktopspaceSmall: 20px;
$desktopspaceMedium: 40px;
$desktopspaceLarge: 80px;
$desktopspaceXL: 160px;

/* Tablet Styles */

$bodyTablet: 30px; 

/* Mobile Styles */

$bodyMobile: 24px; 
$mobilespaceXS: 8px;
$mobilespaceSmall: 16px;
$mobilespaceMedium: 32px;
$mobilespaceLarge: 64px;
$mobilespaceXL: 128px;

/* Global Settings */

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  font-size: $bodyDesktop;
}

.site-wrapper{
  padding: 0 $desktopspaceMedium 0 $desktopspaceMedium;
}

/* Typography */

@font-face {
  font-family: "sohne";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/soehne-web-buch.woff") format("woff"),
       url("../fonts/soehne-web-buch.woff2") format("woff2");
}

@font-face {
  font-family: "sohne";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/soehne-web-dreiviertelfett.woff") format("woff"),
       url("../fonts/soehne-web-dreiviertelfett.woff2") format("woff2");
}

h1, h2, h3, h4, p {
  font-weight: normal;
}

h1 {
  font-size: 7em;
  letter-spacing: -5px;
  line-height: 1;
  padding-bottom: $desktopspaceMedium;
}

p {
  font-size: 1em;
  letter-spacing: -0.3px;
  line-height: 1.3;
}

p.meta {
  font-size: .6em;
  letter-spacing: -.1px;
  line-height: 1.3;
}

a {
  color: $bodyColor;
  text-decoration: none;
  border-bottom: solid 2px $bodyColor;
}

a.play {
  background-color: $bodyColor;
  background-image: -webkit-linear-gradient(top left, #00D1FF, #FF00E2);
  background-image: gradient(top left, #00D1FF, #FF00E2);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  // background: 
  //             linear-gradient(to right, #00D1FF, #FF00E2);
  // background-origin: padding-box, border-box;
  // background-repeat: no-repeat;
  // border-bottom: 2px solid transparent;
}

.play-link {
  display: inline;
}

/* Navigation */

nav {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 25px 0 25px 0;
  border-bottom: 2px solid $bodyColor;
}

.nav-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

nav .play{
  margin-left: 30px;
}

.nav-title a {
  text-decoration: none;
  border-bottom: none;
}

.wrapper {
  background: linear-gradient(#1A1A1A, #1A1A1A), 
              linear-gradient(to right, #00D1FF, #FF00E2);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border-bottom: 2px solid transparent;
}

/* Grid */

.content {
  display: grid;
  grid-template-columns: 80px 1fr 40px 1fr 40px 1fr 80px;
}

.page-title{
  grid-column-start: 1;
  grid-column-end: 8;
  padding-bottom: $desktopspaceMedium;
}

.full-width {
  grid-column-start: 1;
  grid-column-end: 8;
  padding-bottom: $desktopspaceLarge;
}

.left-2{
  grid-column-start: 2;
  grid-column-end: 5;
  padding-bottom: $desktopspaceLarge;
}

.right-2{
  grid-column-start: 4;
  grid-column-end: 7;
  padding-bottom: $desktopspaceLarge;
}

.left-1{
  grid-column-start: 2;
  grid-column-end: 3;
  padding-bottom: $desktopspaceLarge;
}

.center-1{
  grid-column-start: 4;
  grid-column-end: 5;
  padding-bottom: $desktopspaceLarge;
}

.right-1{
  grid-column-start: 6;
  grid-column-end: 7;
  padding-bottom: $desktopspaceLarge;
}

/* Content */

.play-h1 {
  background-color: $bodyColor;
  max-width: 460px;
  background-image: -webkit-linear-gradient(top left, #00D1FF, #FF00E2);
  background-image: gradient(top left, #00D1FF, #FF00E2);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

/* Project Block Styling */

.project-wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.project-image{
  padding-bottom: $desktopspaceXS;
}

.project-date{
  color: $textMuted;
  padding-bottom: $desktopspaceXS;
}

.project-tag{
  padding-top: $desktopspaceXS;
  display: flex;
  font-size: .6em;
  text-decoration: none;
  font-style: normal;
}

.project-tag-casestudy{
  padding-right: $desktopspaceXS;
  color: $textYellow;
}

.project-tag-inprocess{
  padding-right: $desktopspaceXS;
  color: $textBlue;
}

.project-tag-blog{
  padding-right: $desktopspaceXS;
  color: $textPurple; 
}
